import { BeagleAngularConfig, BeagleAngularUIService } from '../types'
import createCoreBeagleUIService, { DefaultSchema } from 'beagle-web'

export abstract class AbstractBeagleProvider {
  private service: BeagleAngularUIService | undefined

  start<Schema = DefaultSchema>(baseUrl: string) {
    if (this.service) {
      console.error('Beagle service has already started!')
      return
    }
    // @ts-ignore // fixme
    this.service = createCoreBeagleUIService<Schema>({ baseUrl })
  }

  getBeagleUIService() {
    return this.service
  }
}
